import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';

const WIDTH=240;
const HEIGHT = 50;
const SongWaveform = ({waveform}) => { 

    if (!waveform) {
        return '';
    }

    let parts = [];
    for (let i=0; i < waveform.length - 4; i+=4) {
        let height = waveform[i]/2;
        let p = {
            height,
            y: HEIGHT/2 - height/2,
            r: waveform[i+1],
            g: waveform[i+2],
            b: waveform[i+3]
        };
        parts.push(p);
    }
    console.log(parts);
    return (
        <svg
          width={WIDTH}
          height={HEIGHT}
          className="song-waveform">
          {parts.map((part, i) => <rect x={i*(WIDTH/parts.length)} y={part.y} width={2} height={part.height} fill={`rgb(${part.r}, ${part.g}, ${part.b})`}/>)}
        </svg>
    );
};

export default SongWaveform;
